.infinitescroll .image-grid {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: minmax(50px, 200px);
}

.infinitescroll .image {
  background: #fff;
  padding: 3px;
  border-radius: 3px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-transform;
  transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) transform;
  transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) transform, 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55) -webkit-transform;
}

.infinitescroll .image:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.infinitescroll .image img {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.infinitescroll .image:nth-child(3n) {
  grid-column-end: span 2;
}

